code,
* {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  background-color: #f0f4f9;
}

input {
  padding: 1rem;
  margin-bottom: 1rem;
  border-radius: 5px;
}

h1 {
  text-align: center;
  margin-top: 4rem;
  font-size: 6rem;
  width: 50%;
  margin: auto;
  margin-left: 5%;
}

h3 {
  text-align: center;
  padding-top: 1rem;
}

nav {
  display: flex;
  padding-top: 2rem;
  background-color: #fff;
  padding-bottom: 2rem;
  width: 100%;
}

nav h3 {
  width: 20%;
  display: inline-block;
  font-size: 4rem;
  margin-left: 2rem;
}

nav .btn {
  height: 6rem;
  margin-right: 3rem;
  font-size: 3rem;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 10px;
}

.signforms {
  background-color: #fff;
  border: 1px solid black;
  width: fit-content;
  display: flex;
  margin: auto;
  margin-top: 5%;
  padding: 3rem;
  padding-top: 5rem;
  padding-bottom: 5rem;
  margin-bottom: 5%;
}

.signforms .signinform {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.signforms p {
  font-size: 2rem;
  text-align: center;
  margin-top: 1rem;
}

.weightLogContainer h2,
h2 {
  text-align: center;
  margin-bottom: 1rem;
  font-size: 4rem;
}

h3 span,
h1 span {
  font-size: inherit;
  color: #0138cd;
}

.signforms div,
.signforms input,
.signforms button {
  font-size: 2rem;
}

.signforms button {
  width: 100%;
  margin-top: 1rem;
}

.btn {
  padding: 1rem;
  background-color: #0138cd;
  border: none;
  border-radius: 5px;
  color: white;
  cursor: pointer;
}

.btn-secondary {
  background-color: transparent;
  color: #0138cd;
}

.btn:hover {
  background-color: #012da4;
}

.btn-secondary:hover {
  background-color: transparent;
  outline: 1px solid #0138cd;
}

.btn-delete:hover {
  outline: 1px solid red;
  color: red;
}

.btnWeightLog {
  padding: 0.5rem 1rem;
}

/* weightlog css */

.weightLogContainer {
  width: 50%;
  max-width: 800px;
  min-width: 350px;
  margin: auto;
  padding: 2rem;
  font-size: 3rem;
}

.weightLogContainer * {
  font-size: 1.5rem;
}

.individualWeightLog {
  border-radius: 5px;
  margin-bottom: 0.5rem;
  padding: 1rem;
  background-color: #fff;
}

.weightLogTop,
.weightLogBottom {
  display: flex;
}

.weightLogTop {
  margin-bottom: 1rem;
}

.topLeft,
.weightLogBottom p {
  width: 30%;
  display: inline-block;
}

.topRight,
.weightLogBottom .weightLogBottomButtons {
  width: 70%;
  display: inline-block;
}

.time,
.date {
  display: block;
  opacity: 0.8;
}

.date {
  font-weight: bold;
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
  opacity: 1;
}

.weightLogBottomButtons {
  display: flex;
  justify-content: center;
  gap: 4rem;
}

.weightLogBottomButtons div {
  width: fit-content;
  display: inline-block;
  margin-left: 8rem;
}

.weightLogBottomButtons div:first-of-type {
  margin-left: 0;
}

/* add weight form */
.addWeightForm {
  width: 30%;
  max-width: 800px;
  min-width: 300px;
  margin: auto;
  padding: 2rem;
  font-size: 3rem;
  margin-bottom: 8rem;
  background-color: #fff;
  border-radius: 10px;
}

.addWeightForm * {
  font-size: 2rem;
}

.addWeightForm h3 {
  font-size: 3rem;
}

.addWeightForm .inputSection {
  margin-bottom: 2rem;
}

@media only screen and (max-width: 600px) {
  nav h3 {
    display: none;
  }
  nav h1 {
    font-size: 3rem;
  }

  nav .btn {
    font-size: 2rem;
    height: 4rem;
  }
}

/*modal for editing log*/

/* #portal * {
  outline: 1px solid red;
} */

#portal .container {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.7);
}
.container .modal {
  z-index: 1;
  width: 350px;
  height: 240px;
  background: white;
  /* display: flex;
  align-items: center;
  justify-content: center; */
  border-radius: 5px;
  position: relative;
  animation: animate 0.3s;
}
.modal .close-btn {
  position: absolute;
  top: 7px;
  right: 7px;
  padding: 8px 11px;
  background: crimson;
  color: white;
  font-weight: bold;
  border: none;
  outline: none;
  border-radius: 2px;
  cursor: pointer;
}

.modal h3 {
  font-size: 3rem;
}
.modal form {
  width: 80%;
  margin: auto;
}
.modal form * {
  font-size: 1.5rem;
}
@keyframes animate {
  from {
    transform: scale(0.5);
  }
  to {
    transform: scale(1);
  }
}

/* notification/ error messages */

.message {
  color: green;
  font-size: 2rem;
  text-align: center;
  padding: 10px;
  margin-bottom: 10px;
  width: 30%;
  min-width: 300px;
  margin: auto;
}

.error {
  color: red;
  font-size: 2rem;
  text-align: center;
  padding: 10px;
  margin-bottom: 10px;
  width: 30%;
  min-width: 300px;
  margin: auto;
}
